import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Camera from '@/components/Camera'
import Rank from '@/components/Rank'
import Member from '@/components/Member'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/rank',
      name: 'Hello',
      component: Rank
    },
    {
      path: '/1',
      name: 'Hello1',
      component: Camera
    },
    {
      path: '/member',
      name: 'Member',
      component: Member
    }
  ]
})



